.app {
  text-align: center;
  padding: 20px;
  font-family: Arial, sans-serif;
  max-width: 600px; /* Set a maximum width */
  margin: 0 auto;   /* Center the container */
}

h1 {
  color: #000; /* Change the text color if necessary */
}

.task-input {
  margin-bottom: 20px;
}

.task-input input {
  padding: 10px;
  font-size: 16px;
  width: 300px; /* Adjust width as needed */
}

.task-input button {
  padding: 10px;
  font-size: 16px;
  margin-left: 10px;
  background-color: #4CAF50; /* Green background */
  color: white;
  border: none;
  cursor: pointer;
}

.task-input button:hover {
  background-color: #45a049;
}

.task-list {
  list-style-type: none;
  padding: 0;
  margin: 0; /* Remove default margin */
}

.task-list li {
  background-color: #333;
  color: white;
  margin: 10px 0;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px; /* Add border radius */
}

.task-list li.completed {
  text-decoration: line-through;
  color: gray;
}

.task-list button {
  background-color: rgb(255, 75, 105);
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  margin-left: 10px;
  border-radius: 3px; /* Add border radius */
}

.task-list button:hover {
  background-color: darkred;
}

.task-list button:last-of-type {
  background-color: rgb(147, 147, 209);
}

.task-list button:last-of-type:hover {
  background-color: rgb(226, 188, 106);
}

.task-list button:first-of-type {
  background-color: rgb(199, 199, 199);
}

.task-list button:first-of-type:hover {
  background-color: rgb(255, 0, 106);
}

.task-list button:nth-of-type(2) {
  background-color: rgb(199, 199, 199);
}

.task-list button:nth-of-type(2):hover {
  background-color: darkpurple;
}
